import React, { Component } from 'react';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { autoFillOnLoad: false };
  }

  async componentDidMount() {
    this.setState(await browser.storage.local.get('autoFillOnLoad'));
  }

  handleChange(event) {
    const autoFillOnLoad = event.target.checked;
    this.setState({ autoFillOnLoad });
    browser.storage.local.set({ autoFillOnLoad });
  }

  render() {
    return (
      <div>
        <p>Automatically fill login information:</p>
        <label className="switch">
          <input
            checked={this.state.autoFillOnLoad}
            onChange={e => this.handleChange(e)}
            type="checkbox"
          />
          <span className="slider round" />
        </label>
      </div>
    );
  }
}

export default App;
